<template>
    <div class="content">
        <div class="head" v-if="err==1">
            <i class="aticonfont aticon-close-circle-fill"></i>
            <div>操作异常</div>
            <div class="prompt">请返回检查操作是否正常</div>
        </div>
        <div class="head" v-else-if="err==2">
            <i class="aticonfont aticon-check-circle-fill"></i>
            <div>认证成功</div>
            <div class="prompt">该账号以认证成功，无须重复认证</div>
        </div>
        <div class="head" v-else>
            <i class="aticonfont aticon-safetycertificate"></i>
            <div>辅助认证</div>
        </div>
        <div v-if="err==0">
            <el-form class="form" :model="form">
                <el-form-item>
                    <el-input v-model="form.name" placeholder="请输入真实姓名"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="form.id" placeholder="请输入身份证号码"></el-input>
                </el-form-item>
                <el-button class="submit" type="primary" @click="submit">提交继续认证</el-button>
            </el-form>
        </div>
    </div>
</template>

<script>
import request from "@/request";

export default {
    name: "Verify",
    metaInfo: {
        title: '辅助认证',
    },
    beforeCreate() {
        document.addEventListener('touchmove', {
            function(e){
                e.preventDefault()
            }
        }, false);
        document.body.addEventListener('touchmove', function (e) {
            e.preventDefault();
        }, {passive: false});
    },
    beforeMount() {
        if (this.$route.params.account == undefined) {
            this.err = 1;
        }else{
            request.Douyin.info(this.$route.params.account).then(res=>{
                if (res.data.verify > 0) {
                    this.err = 2;
                }
            });
        }
    },
    data: function () {
        return {
            err: 0,
            form: {
                name: '',
                id: ''
            }
        };
    },
    methods: {
        submit: function () {
            request.Douyin.submit({...this.form, account: this.$route.params.account}).then(res=>{
                if (res.status == -4) {
                    this.$message.error('已认证成功，无需重复认证！');
                }else if(res.status == -3) {
                    this.$message.error(res.data);
                }else if(res.status == -2) {
                    this.$message.error('账号不存在！');
                }else if(res.status == -1) {
                    this.$message.error('请填写完整后提交');
                }else if(res.status > 0){
                    // 成功
                    window.location.href = res.data;
                }else{
                    this.$message.error('操作失败请重试');
                }
            });
        }
    }
}
</script>

